@import '../common/styles/sass/mixins';
@import '../common/styles/sass/variables';

.nav {
    display: flex;
    justify-content: space-between;
    width:400px;
    flex-wrap: wrap;
    margin-right: 10px;
    .link{
        @include text(1.2rem, 0, 0.5rem, 1.2, $textColor);
        font-weight: 600;
        border-radius: 4px;
        border: 1px solid transparent;
        padding: 5px 10px;
        box-sizing: border-box;
        &:hover{
            border: 1px solid #33a3b6;
            background-color: lighten($bgThirdColor,10%);
            box-shadow: 0 1px 5px rgba(5, 5, 6, 0.95);
            transition: all .3s ease 0s;
        }
    }
}