@import '../common/styles/sass/variables.scss';

.footerBlock {
  //min-height: 10vh;
  background-color: $bgThirdColor;

  .footerContainer {
    flex-direction: column;
    justify-content: space-between;

    .title {
      margin-top: 10px;
      margin-bottom: 10px;
      text-align: center;
    }

    .footer {
      display: flex;
      justify-content: space-between;
      width: 250px;
      margin-top: 5px;
      margin-bottom: 5px;

      .footerElements {
        width: 30px;
        height: 30px;
        background-size: cover;
        background-repeat: no-repeat;
        margin-bottom: 10px;
        box-shadow: 0 1px 5px rgba(5, 5, 6, 0.15);
        transition: all .3s ease 0s;
        &:hover{
          box-shadow: 0 1px 5px rgba(5, 5, 6, 0.95);;
        }

      }
    }
  }
}






